.profile {
  margin-top: 50px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
  background-color: grey;
}
.description {
  background-color: yellowgreen;
  text-align: center;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.5);
}

.avatar {
  border-radius: 50%;
  width: 100px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid gray;
}

.name {
  margin-top: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.25;
}

.location,
.tag {
  margin-top: 10px;
  font-size: 18px;
  line-height: 1.25;
}

.stats {
  display: flex;
  width: 300px;
  margin-top: 10px;

  text-align: center;
  background-color: dimgrey;

  box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.5);
}

.stats li {
  padding: 20px 10px;
  width: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px solid gray;
}

.label {
  font-size: 18px;
  line-height: 1.25;
  margin-bottom: 8px;
}

.quantity {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.25;
  letter-spacing: 1px;
}
